import { useState } from "react";
import "./App.css";
import { Testimonials } from "./Data";
import { FaQuoteLeft, FaChevronLeft, FaChevronRight } from "react-icons/fa";

function App() {
  let [index, setIndex] = useState(0);
  const { name, position, Image, review } = Testimonials[index];

  const checkNumber = (number) => {
    if (number > Testimonials.length - 1) {
      return 0;
    } else if (number < 0) {
      return Testimonials.length - 1;
    }
    return number;
  };

  const increase = () => {
    setIndex((index) => {
      let newIndex = index + 1;
      return checkNumber(newIndex);
    });
  };
  const decrease = () => {
    setIndex((index) => {
      let newIndex = index - 1;
      return checkNumber(newIndex);
    });
  };
  const randomNumber = () => {
    let value = Math.floor(Math.random() * Testimonials.length);
    if (value === index) {
      value = index + 1;
    }
    setIndex(checkNumber(value));
  };
  return (
    <div className="App">
      <div className="container">
        <div className="heading">
          <h1>Our Reviews</h1>
        </div>
        <div className="review-box">
          <div className="image-sec">
            <img src={Image} alt="name" />
            <div className="overlay"></div>
            <div className="quote-icon">
              <FaQuoteLeft />
            </div>
          </div>
          <div className="user-info">
            <h4 className="person-name">{name}</h4>
            <span className="position">{position}</span>
          </div>
          <div className="review">
            <p>{review}</p>
          </div>
          <div className="controls">
            <button className="prev" onClick={increase}>
              <FaChevronLeft />
            </button>
            <button className="next" onClick={decrease}>
              <FaChevronRight />
            </button>
            <button className="review-btn" onClick={randomNumber}>
              Random Review
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
